.discover {
//padding-right: 45px;
//padding-left: 20px;
margin-right: auto;
margin-left: auto;
}


.navbar {
    h3 {
        color: #AFAFAF;
        font-size: 2em;
        font-weight: 300;
        display: inline-block;
        margin: 0px;
    }
    button.browse {
        display: inline-block;
        margin: 0px;
        position: relative;
        top: -2px;
    }
}

.first {
    position: relative;
    left: 10px;
    top: 3px;
    padding-left: 40px;
}

.second {
    position: relative;
    top: 3px;
}

.navbar-accordance {
   border-radius: 0;
}

.brand-accordance {
    padding: 0;
}


.breadcrumb {
    /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#4b4a46+0,4e4f53+4,4e4d4b+17,4b4b49+26,4c474b+35,454744+48,3d3d3d+91,343434+96,555555+100 */
    background: #4b4a46; /* Old browsers */
    background: -moz-linear-gradient(top, #4b4a46 0%, #4e4f53 4%, #4e4d4b 17%, #4b4b49 26%, #4c474b 35%, #454744 48%, #3d3d3d 91%, #343434 96%, #555555 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(top, #4b4a46 0%,#4e4f53 4%,#4e4d4b 17%,#4b4b49 26%,#4c474b 35%,#454744 48%,#3d3d3d 91%,#343434 96%,#555555 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, #4b4a46 0%,#4e4f53 4%,#4e4d4b 17%,#4b4b49 26%,#4c474b 35%,#454744 48%,#3d3d3d 91%,#343434 96%,#555555 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#4b4a46', endColorstr='#555555',GradientType=0 ); /* IE6-9 */
    .active {
        color: #fff;
    }
}
